<template>
<div>
  <my-input
      v-model="email"
      :class="{
          'error': isError
        }"
      type="text"
      class="element input"
      @keyup.enter="sendEmail"
      placeholder="Email"></my-input>
  <input
      type="checkbox"
      class="custom-checkbox"
      id="politic"
      name="politic"
      ref="checkbox"
      :class="{
          'error': isErrorCheckbox
        }"
      @change="this.isCheckbox = !this.isCheckbox"
      value="yes">
  <label for="politic">Согласен с политикой конфиденциальности</label>
  <my-button
      class="element button"
      @click="sendEmail"
  >Отправить</my-button>
</div>
</template>

<script>
export default {
  name: 'my-email',
  data() {
    return {
      email: '',
      isError: false,
      isErrorCheckbox: false,
      isCheckbox: false
    }
  },
  methods: {
    checkEmail(email) {
      var re = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
      return re.test(String(email).toLowerCase());
    },
    sendEmail() {
      this.isErrorCheckbox = false
      this.isError = false
      if (!this.isCheckbox) {
        this.isErrorCheckbox = true
      }
      if (this.email === '' || !this.checkEmail(this.email)) {
        this.isError = true
      }
      if (this.isCheckbox && this.email !== '' && this.checkEmail(this.email)) {
        this.$emit('sendEmail', this.email)
        this.email = ''
        this.$refs.checkbox.checked = false
      }
    }
  },
}
</script>

<style scoped>
.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
}
.custom-checkbox+label::before {
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}
.custom-checkbox:checked+label::before {
  border-color: #0b76ef;
  background-color: #0b76ef;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}
.error {
  border: 1px solid #c22121;
  outline: 1px solid #c22121;
  box-shadow: 0 0 10px #c22121;
}
.error+label::before {
  border: 1px solid #c22121;
}
.error:focus {
  border: 1px solid #c22121;
  box-shadow: 0 0 10px #c22121;
}
.element {
  margin: 10px 0;
  border-radius: 10px;
  color: #fff;
}
.input, .input:focus {
  border: 1px solid grey;
  padding: 10px;
  width: 100%;
}
.button {
  background: #2ecc71;
  border: 1px solid #2ecc71;
}
</style>