<template>
  <transition name="fade">
<div class="dialog"
     v-if="show"
     @click="hideDialog">
    <div @click.stop class="dialog__content">
      <div
          @click="hideDialog"
          class="dialog-close"></div>
      <slot></slot>
    </div>
</div>
  </transition>
</template>

<script>
import toggleMixin from "@/mixins/toggleMixin";

export default {
  name: 'my-dialog',
  mixins: [toggleMixin]
}
</script>

<style scoped>
.dialog {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  display: flex;
  padding: 10px;
}
.dialog__content {
  margin: auto;
  background: white;
  border-radius: 12px;
  min-height: 50px;
  min-width: 300px;
  padding: 35px 20px 20px 20px;
  position: relative;
}
.dialog-close {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 12px;
  cursor: pointer;
}
.dialog-close::after, .dialog-close::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 34px;
  height: 1px;
  background: black;
}
.dialog-close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}
.dialog-close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
</style>