<template>
<div class="container">
  <h2>{{ searchCategory ? searchCategory : 'Поиск' }}</h2>
  <my-search @send="send"></my-search>
    <h3 v-if="isSearch">Поиск по запросу: {{ search }}</h3>
  <food-menu class="food-menu" :searchCategory="searchCategory" :search="search" :limit="200" @cancel="cancel"></food-menu>
</div>
</template>

<script>
import FoodMenu from "@/components/FoodMenu";
export default {
  components: {FoodMenu},
  data() {
    return {
      search: '',
      isSearch: false,
      searchCategory: ''
    }
  },
  methods: {
    send(search) {
      this.search = search
      this.isSearch = true
    },
    cancel() {
      this.isSearch = false
      this.searchCategory = ''
    }
  },
  mounted() {
    this.searchCategory = this.$route.query.category
    this.search = this.$route.query.searchQuery
    if (this.search !== undefined) {
      this.isSearch = true
    }
  }
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
h3 {
  @include adaptiv-value("font-size", 20, 16);
}
.menu {
  margin: 20px 0 0 0;
}
.menu-item:not(:last-child) {
  margin: 0 0 10px 0;
}
</style>