<template>
    <nav-bar
        @send="sendPhone">
    </nav-bar>
    <div class="content">
      <router-view>

      </router-view>
      <div
          v-show="isArrow"
          @click="goUp"
          ref="up"
          class="arrow-up">
        Наверх
        <span></span>
      </div>
    </div>
  <div ref="footer" class="footer">
    <div class="container flex">
      <div class="footer__left">
        <div class="footer-image">
          <img src="@/image/logo.png" alt="logo">
        </div>
        <a href="tel: 88005553535">8 (800) 555 35-35</a>
        <div>Внешний вид блюд может отличаться от фотографий на сайте</div>
      </div>
      <div class="footer__right">
        <my-email @sendEmail="sendEmail"></my-email>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isArrow: false
    }
  },
  methods: {
    sendPhone(phone) {
      alert(`Send ${phone}`)
    },
    sendEmail(email) {
      alert(`Send ${email}`)
    },
    goUp() {
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth'
      });
    }
  },
  mounted() {
    window.addEventListener('scroll', () => {
      if ((window.innerHeight - (this.$refs.up.offsetHeight / 2)) > this.$refs.footer.getBoundingClientRect().y) {
        this.$refs.up.classList.remove('black')
        this.$refs.up.style.color = 'white'
        this.$refs.up.classList.add('white')
      } else {
        this.$refs.up.classList.remove('white')
        this.$refs.up.style.color = 'black'
        this.$refs.up.classList.add('black')
      }
      if (window.scrollY > 200) {
        this.isArrow = true
      } else {
        this.isArrow = false
      }
    })
  }
}
</script>

