<template>
  <input :value="modelValue" @input="updateInput" type="text">
</template>

<script>
export default {
  name: 'my-input',
  props: {
    modelValue: [String, Number]
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
</script>

<style scoped>
input {
  box-sizing: border-box;
  border: 1px solid #302b50;
  cursor: pointer;
  padding: 6px 17px;
  background: 0 0;
  font-weight: 400;
  display: block;
  line-height: 122.2222222222%;
  font-size: 18px;
  border-radius: 45px;
  color: #000;
  transition: all .3s;
  position: relative;
}
input:focus, input:active {
  border: 0;
  outline: 0;
}
input.error {
  border: 1px solid #c22121;
  box-shadow: 0 0 10px #c22121;
}
</style>