<template>
  <div class="container">
    <h2>Корзина</h2>
    <div class="bag">
      <table>
        <tr>
          <td>Товар</td>
          <td>Количество</td>
          <td>Цена</td>
        </tr>
        <my-row
            class="row"
            v-for="food in foods.filter(f => f.count !== 0)"
            :food="food"
            @add="addCountFood"
            @sub="subCountFood"
        >
        </my-row>
        <my-loading v-show="isFoodLoading"></my-loading>
      </table>
      <div class="buy">
        <div class="buy__title">Оформление</div>
        <div class="buy__form">
          <div class="form__text">
            <div class="text-count">Всего({{ count() }})</div>
            <div class="text-price">{{ prices() }} ₽</div>
          </div>
          <my-button @click="tobe">Оформить</my-button>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div
          v-show="isMax"
          class="max">Максимум 10!
      </div>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      foods: [],
      foodChoice: [],
      isFoodLoading: false,
      isMax: false
    }
  },
  methods: {
    getRandomArbitrary(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    count() {
      let counts = 0
      this.foods.forEach(f => {
        counts += f.count
      })
      return counts
    },
    tobe() {
      alert('Пока что оформления нет :)')
    },
    prices() {
      let counts = 0
      this.foods.forEach(f => {
        if (f.count !== 0) {
          counts += (f.price * f.count)
        }
      })
      return counts
    },
    async fetchPosts() {
      try {
        this.isFoodLoading = true
        const response = await axios.get('https://jsonplaceholder.typicode.com/photos?_limit=200')
        const responseBody = await axios.get('https://jsonplaceholder.typicode.com/comments')
        this.foods = response.data
        this.foodChoice = response.data.filter(t => t.id <= 30)
        this.foods.forEach(f => {
          f.price = this.getRandomArbitrary(250, 1500)
          f.calories = this.getRandomArbitrary(50, 300)
          const rand = Math.floor(Math.random()*this.foodChoice.length);
          f.category = this.foodChoice[rand].title;
          f.body = responseBody.data[rand].body.charAt(0).toUpperCase() + responseBody.data[rand].body.slice(1)
          f.title = f.title.charAt(0).toUpperCase() + f.title.slice(1)
          f.count = this.getRandomArbitrary(0, 3)
        })
        this.foods.forEach(f => {
          f.category = f.category.match(/([a-zа-яё]+)/i)[0]
        })
        this.isFoodLoading = false
      } catch (e) {
        alert('Error')
      }
    },
    addCountFood(id) {
      const item = this.foods.find(t => t.id === id)
      if (item.count < 10) {
        item.count++
      } else {
        this.isMax = true
        setTimeout(() => {
          this.isMax = false
        }, 500)
      }
    },
    subCountFood(id) {
      const item = this.foods.find(t => t.id === id)
      if (item.count > 0) {
        item.count--
      }
    }
  },
  mounted() {
    this.fetchPosts()
  }
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
table tr:first-child, .buy {
  font-weight: 600;
  font-size: 18px;
  line-height: 122.2222222222%;
}
.form__text {
  margin: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.buy__title {
  padding: 10px;
  font-weight: 600;
  text-align: center;
}
table {
  border-spacing: 15px 10px;
  @media (max-width: 992px) {
    border-spacing: 5px;
  }
  flex: 1 1 auto;
}
table tr {
  vertical-align: top
}
table tr:first-child td:last-child {
  text-align: right;
}
.bag {
  display: flex;
  @include adaptiv-value("gap", 30, 10);
  @media (max-width: 992px) {
    flex-direction: column;
  }
}
.buy {
  flex: 0 0 300px;
  @media (max-width: 768px) {
    flex: 1 1 auto;
  }
  font-weight: 500;
}
.max {
  position: fixed;
  right: 25px;
  bottom: 25px;
  background: #fff;
  border: 3px solid #ffe7a8;
  box-sizing: border-box;
  border-radius: 45px;
  padding: 15px;
  font-size: 20px;
  line-height: 123.3333333333%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
</style>