<template>
  <form @submit.prevent>
    <my-input
        v-model="post.title"
        v-focus
        type="text"
        placeholder="Title"></my-input>
    <my-button
        @click="createPost"
    >Отправить</my-button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      post: {
        title: '',
        body: ''
      }
    }
  },
  methods: {
    createPost() {
      this.post.id = Date.now()
      this.$emit('create', this.post)
      this.post = {
        title: '',
        body: ''
      }
    }
  }
}
</script>

<style>
form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin: 15px 0 0;
}
form input, button {
  width: 100%;
}
form button {
}
</style>