<template>
<div class="navbar" ref="navbar">
  <div class="navbar__left">
    <div class="logo" @click="$router.push('/')">
      <img src="../../../public/favicon.png" alt="logo">
      <span>EcoHouse</span>
    </div>
    <div class="navbar__phone">
      <a href="tel: 88005553535">8 (800) 555 35-35</a>
      <my-button
          @click="showDialog">
        Позвоните мне
      </my-button>
    </div>
  </div>
  <div class="navbar__right">
    <div class="navbar__btns">
          <my-button @click="routeLink">Блюда</my-button>
          <my-button @click="$router.push('/bag')">Корзина</my-button>
    </div>
    <div @click="clickBurger" class="burger"><span></span></div>
  </div>
  <transition name="fade">
  <div v-show="isBurger" ref="burger-menu" class="navbar__burger"></div>
  </transition>
</div>
  <my-dialog v-model:show="dialogVisible">
    <h2>Обратный звонок</h2>
    <SendPhone
        @send="sendPhone">
      ></SendPhone>
  </my-dialog>
</template>

<script>
import PostForm from "@/components/PostForm";
import SendPhone from "@/components/SendPhone";
export default {
  name: 'nav-bar',
  components: {SendPhone, PostForm},
  data() {
    return {
      dialogVisible: false,
      isBurger: false
    }
  },
  methods: {
    clickBurger() {
      this.isBurger = !this.isBurger
    },
    showDialog() {
      this.dialogVisible = true
      this.isBurger = false
      document.body.style.overflow = 'hidden';
    },
    sendPhone(phone) {
      this.$emit('send', phone)
      this.dialogVisible = false
    },
    routeLink() {
      if (this.$router.currentRoute._value.path === '/menu') {
        location.reload()
      }
      this.$router.push('/menu')
    }
  },
  mounted() {
    const phone = document.querySelector('.navbar__phone');
    const click = document.querySelector('.navbar__btns');
    const buttons = document.querySelectorAll('.navbar__btns')
    const button = document.querySelector('.logo')
    buttons.forEach(b => {
      b.addEventListener('click', () => {
        this.isBurger = false
      })
    })
    button.addEventListener('click', () => {
      this.isBurger = false
    })
    if (window.innerWidth <= 768) {
      this.$refs["burger-menu"].insertAdjacentElement('beforeend', phone);
      this.$refs["burger-menu"].insertAdjacentElement('beforeend', click);
    }
    this.$refs["burger-menu"].style.top = `${this.$refs.navbar.offsetHeight}px`
  },
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
    #{$property}: $startSize + px;
    @media (max-width: #{1120 + px}) {
      #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
    }
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  cursor: pointer;
  font-weight: 300;
  line-height: 150%;
  font-size: 30px;
  color: #ff8c00;
  @media (max-width: 768px) {
    span {
      display: none;
    }
  }
}
.burger {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: none;
  @media (max-width: 768px) {
    z-index: 5;
    cursor: pointer;
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    & span,
    &::after,
    &::before {
      position: absolute;
      height: 10%;
      left: 0;
      width: 100%;
      transition: all 0.3s ease 0s;
      background: #2c3e50;
    }
    &::after,
    &::before {
      content: "";
    }
    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
    & span {
      top: 50%;
      transform: scale(1) translate(0px, -50%);
    }
    &.active {
      span {
        transform: scale(0);
      }
      &::before {
        top: 50%;
        transform: rotate(-45deg) translate(0, -50%);
      }
      &::after {
        bottom: 50%;
        transform: rotate(45deg) translate(0, 50%);
      }
    }
  }
}
.navbar__burger {
    transition: transform 0.3s ease;
    transform: translate(0, 0);
    position: fixed;
    overflow: auto;
    left: 0;
  padding: 15px;
  background: #fffb96;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 50;
}
h2 {
  margin: 0 0 20px 0;
  color: black;
}
.navbar {
  padding: 15px;
  background: #fffb96;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}
.navbar__btns, .navbar__left, .navbar__right {
  display: flex;
  align-items: center;
  gap: 15px;
}
.navbar__btns {
  @media (max-width: 768px) {
    flex-direction: column;
  }
}
.navbar__phone {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 5px;
  @media (max-width: 768px) {
    gap: 15px;
    margin: 0 0 15px 0;
  }
}
.navbar__phone a {
  text-decoration: none;
  @include adaptiv-value("font-size", 20, 16);
  color: #2c3e50;
}
</style>