<template>
  <div class="food"
       @click="$router.push({name: 'menu', query: { category: food.title }})">
    <div class="food-image"><img :src="food.url" alt="image"></div>
    <div>{{ food.title }}</div>
  </div>
</template>

<script>
export default {
  name: 'my-button-food',
  props: {
    food: {
      type: Object,
      required: true
    }
  }
}
</script>

<style lang="scss">
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
.food {
  background: #ffe7a8;
  padding: 10px 20px;
  box-shadow: 10px 8px 4px rgba(0, 0, 0, 0.25);
  border-radius: 45px;
  display: flex;
  @include adaptiv-value("font-size", 20, 16);
  align-items: center;
  gap: 15px;
  cursor: pointer;
  justify-content: space-evenly;
  transition: all .3s;
  flex: 1 1 auto;
}
.food:hover {
  transform: translate(4px, 4px);
  box-shadow: 6px 5px 3px rgba(0, 0, 0, 0.25);
}
.food-image {
  width: 50px;
  height: 50px;
}
.food-image img {
  max-width: 100%;
}
</style>