<template>
<div class="container">
  <h2
      @click="$router.push('/menu')"
      class="route"
  >Найти нужное блюдо
    <div class="arrow">
      <span></span>
    </div>
  </h2>
  <h2>Акции дня</h2>
  <food-menu :limit="4"></food-menu>
  <h2>Выберите любимый продукт, а мы подберём блюдо</h2>
  <food-list
      @searchCategory="searchCategory"
      :foods="foodChoice"></food-list>
  <h2>Вас могут заинтересовать наши категории блюд</h2>
  <food-list

      @searchCategory="searchCategory"
      :foods="category"></food-list>
</div>
</template>

<script>
import FoodList from "@/components/FoodList";
import FoodMenu from "@/components/FoodMenu";
import axios from "axios";

export default {
  components: {FoodMenu, FoodList},
  data() {
    return {
      foodChoice: [],
      isFoodLoading: false,
      category: [
        { id: 1, title: 'Супы', url: 'https://via.placeholder.com/600/92c952' },
        { id: 2, title: 'Вторые блюда', url: 'https://via.placeholder.com/600/771796' },
        { id: 3, title: 'Салаты', url: 'https://via.placeholder.com/600/24f355' },
        { id: 4, title: 'Десерты', url: 'https://via.placeholder.com/600/d32776' }
      ],
    }
  },
  methods: {
    async fetchPosts() {
      try {
        const responseCategory = await axios.get('https://jsonplaceholder.typicode.com/photos?_limit=34')
        this.foodChoice = responseCategory.data
        this.foodChoice.forEach(f => {
          f.title = f.title.match(/([a-zа-яё]+)/i)[0]
        })
      } catch (e) {
        alert('Error')
      }
    }
  },
  mounted() {
    this.fetchPosts()
  },
}
</script>

<style lang="scss">
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
h2 {
  font-weight: 600;
  @include adaptiv-value("font-size", 36, 24);
  line-height: 111.1111111111%;
  margin: 35px 0;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: all 0.6s;
}
.route {
  cursor: pointer;
}
.route:hover {
  transform: translate(100px, 0);
}
.route:hover .arrow {
  transform: translate(20px, 0);
}
.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 3px;
  background: black;
  position: relative;
  transition: all 0.6s;
}
.arrow span::before, .arrow span::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 20px;
  height: 3px;
  background: black;
}
.arrow span::before {
  transform: rotate(45deg) translate(-15%, -300%);
}
.arrow span::after {
  transform: rotate(-45deg) translate(0, 200%);
}
</style>