<template>
  <div class="item">
    <div class="image">
      <img :src="food.url" alt="image">
    </div>
    <div class="about">
      <div
          @click="$emit('open', food)"
          class="about__title">
        {{ food.title }}
      </div>
      <div class="about__body">
        {{ food.body }}
      </div>
      <div class="about__category-food">
        Категория: {{ food.categoryFood }}
      </div>
      <div class="about__price">
        {{ food.calories }} ккал / 100 г. - <span>{{ food.price }}</span> ₽
      </div>
      <div class="btn-buy">
        <div
            @click="clickActive(food.id)"
            :class="{
          'active': (food.count > 0) ? isActive = true : isActive = false
            }"
            class="block__buy">
          Заказать
        </div>
        <div
            :class="{
          'active': (food.count > 0) ? isActive = true : isActive = false
            }"
            class="about__buy">
          <div class="buy-sub"
               @click="subCount(food.id, food.count)">-</div>
          <div class="buy-count">{{ food.count }}</div>
          <div class="buy-add"
               @click="addCount(food.id)">+</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: false
    }
  },
  props: {
    food: {
      type: Object,
      required: true
    }
  },
  methods: {
    clickActive(id) {
      this.isActive = true
      this.$emit('addCount', id)
    },
    subCount(id, count) {
      if (count > 1) {
        this.$emit('subCount', id)
      } else {
        this.isActive = false
        this.$emit('subCount', id)
      }
    },
    addCount(id) {
      this.$emit('addCount', id)
    },
  }
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
$addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
.item {
  display: flex;
  @include adaptiv-value("gap", 15, 5);
  width: 100%;
}
.about__category-food {
  @include adaptiv-value("font-size", 20, 14);
}
.image {
  width: 100%;
  @include adaptiv-value("max-width", 400, 300);
  @include adaptiv-value("max-height", 400, 300);
  @include adaptiv-value("min-width", 400, 300);
  @include adaptiv-value("min-height", 400, 300);
  overflow: hidden;
  border-radius: 10px;
}
.sale-item .image {
  @include adaptiv-value("max-width", 300, 200);
  @include adaptiv-value("max-height", 300, 200);
  @include adaptiv-value("min-width", 300, 200);
  @include adaptiv-value("min-height", 300, 200);
}
.image img {
  max-width: 100%;
  width: 100%;
}
.about {
  display: flex;
  @include adaptiv-value("gap", 15, 5);
  flex-direction: column;
  @include adaptiv-value("padding", 15, 5);
  font-weight: 500;
}
.about__title {
  @include adaptiv-value("font-size", 30, 20);
  cursor: pointer;
  line-height: 123.3333333333%;
}
.about__body {
  flex: 1 1 auto;
  @include adaptiv-value("font-size", 18, 14);
  line-height: 122.2222222222%;
}
.sale-item .about {
  flex: 1 1 auto;
}
.sale-item .about__body {
  flex: 1 1 auto;
}
.about__price {
  font-size: 24px;
  @include adaptiv-value("font-size", 24, 18);
  line-height: 123.3333333333%;
}
.about__price span {
  @include adaptiv-value("font-size", 28, 20);
}
.btn-buy {
  width: max-content;
  overflow: hidden;
  white-space: nowrap;
  align-items: center;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  position: relative;
  z-index: 0;
  @media (max-width: 600px) {
    margin: 0 auto;
  }
}
.sale-item .btn-buy {
  margin: 0 auto;
}
.block__buy {
  width: 110px;
  border-radius: 50px;
  border: 1px solid black;
  padding: 5px;
  cursor: pointer;
  position: relative;
  @include adaptiv-value("font-size", 20, 16);
  z-index: 1;
  transition: all 0.3s;
}
.block__buy.active {
  transform: scale(0.9);
}
.about__buy {
  position: absolute;
  top: 0;
  left: 0;
  @include adaptiv-value("font-size", 20, 16);
  z-index: 2;
  width: 110px;
  background: #fff9e2;
  transform: translate(0, -100%);
  display: flex;
  transition: all 0.4s;
  justify-content: center;
  border-radius: 50px;
  border: 1px solid black;
  padding: 5px;
  align-items: center;
}
.about__buy.active {
  transform: translate(0, 0);
}
.buy-sub, .buy-add {
  cursor: pointer;
  width: 45px;
}
.buy-count {
  width: 20px;
}
</style>