<template>
  <div>
    <my-input
        v-model="phone"
        v-focus
        :class="{
          'error': isError
        }"
        type="text"
        @keyup.enter="sendPhone"
    placeholder="+7 (999) 999 99-99">
    </my-input>
    <my-button
        @click="sendPhone"
    >Отправить</my-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone: '',
      isError: false
    }
  },
  methods: {
    sendPhone() {
      if (this.phone !== '' && this.checkPhone(this.phone)) {
        this.$emit('send', this.phone)
        this.phone = ''
        this.isError = false
      } else {
        this.isError = true
      }
    },
    checkPhone(phone) {
      let regex = /^(\+7|7|8)?[\s\-]?\(?[489][0-9]{2}\)?[\s\-]?[0-9]{3}[\s\-]?[0-9]{2}[\s\-]?[0-9]{2}$/;
      return regex.test(phone);
    }
  }
}
</script>

<style scoped>
div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 15px;
  margin: 10px 0 0 0;
}
input:focus {
  border: 1px solid black
}
* {
  width: 100%;
}
.error {
  border: 1px solid #c22121;
  box-shadow: 0 0 10px #c22121;
}
.error:focus {
  border: 1px solid #c22121;
  box-shadow: 0 0 10px #c22121;
}
</style>