<template>
  <tr>
    <td>
      <div class="info">
        <div class="img"><img :src="food.url" alt="image"></div>
        <div class="info-about">
          <div>{{ food.title }}</div>
          <div>{{ food.calories }} ккал / 100 г.</div>
        </div>
      </div>
    </td>
    <td class="count">
      <div @click="$emit('sub', food.id)">-</div>
      <div class="number">{{ food.count }}</div>
      <div @click="$emit('add', food.id)">+</div>
    </td>
    <td class="price">{{ food.price * food.count }} ₽</td>
  </tr>
</template>

<script>
export default {
  name: 'my-row',
  props: {
    food: {
      type: Object,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
.count {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  min-height: 100%;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
tr {
  @include adaptiv-value("font-size", 18, 14);
  position: relative;
  &::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 0.5px;
    background: black;
  }
}
.count div:first-child,.count div:last-child {
  cursor: pointer;
  font-size: 24px;
}
.info .img {
  @include adaptiv-value("max-width", 200, 100);
  @include adaptiv-value("max-height", 200, 100);
  @include adaptiv-value("min-width", 200, 100);
  @include adaptiv-value("min-height", 200, 100);
  margin: 0 0 10px 0;
  @media (max-width: 992px) {
    margin: 0 0 5px 0;
  }
}
.info-about div:first-child {
  margin: 0 0 10px 0;
  font-weight: 500;
  @media (max-width: 480px) {
    margin: 0 0 5px 0;
  }
}
tr img {
  max-width: 100%;
}
tr td:last-child {
  font-weight: 500;
  white-space: nowrap;
  text-align: right;
  width: 80px;
}
.number {
  width: 20px;
}
.info {
  display: flex;
  @include adaptiv-value("gap", 15, 5);
  @media (max-width: 480px) {
    flex-wrap: wrap;
    margin: 0 0 5px 0;
  }
}
.price {
  font-weight: 500;
}
</style>