<template>
  <select v-model="modelValue" @change="changeOption">
    <option value="" disabled>Select</option>
    <option
        v-for="option in options"
        :key="option.value"
        :value="option.value">
      {{ option.name }}
    </option>
  </select>
</template>

<script>
export default {
  name: 'my-select',
  props: {
    modelValue: {
      type: String,
    },
    options: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    changeOption(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
</script>

<style scoped>
select {
  padding: 10px;
  margin: 0;
  border-radius: 5px;
  border: 1px solid #38f874;
}
</style>