<template>
  <div>
    <div class="search-place">
      <my-input
          class="search"
          v-model="searchQuery"
          placeholder="Введите название блюда..."
          @keyup.enter="sendSearch(searchQuery)"
          :class="{
            'move': this.move === true
          }"
      >
      </my-input>
      <my-button
          @click="sendSearch(searchQuery)">Поиск</my-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'my-search',
  data() {
    return {
      searchQuery: '',
      move: false,
    }
  },
  methods: {
    sendSearch(search) {
      if (search !== '') {
        this.searchQuery = ''
        if (this.$router.currentRoute._value.path === '/menu') {
          this.$emit('send', search)
        }
      } else {
        this.move = true
        setTimeout(() => {
          this.move = false
        }, 500)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
.search-place {
  background: #fff;
  border: 3px solid #ffe7a8;
  box-sizing: border-box;
  border-radius: 45px;
  margin: 35px 0;
  display: flex;
  position: relative;
  align-items: center;
  @include adaptiv-value("gap", 15, 5);
  input {
    width: 50px;
  }
}
.search-place button {
  width: auto;
  border: 0;
  outline: 3px solid #ffe7a8;
  padding: 20px 30px;
  @include adaptiv-value("padding-top", 20, 10);
  @include adaptiv-value("padding-bottom", 20, 10);
  @include adaptiv-value("padding-right", 30, 10);
  @include adaptiv-value("padding-left", 30, 10);
  @include adaptiv-value("font-size", 24, 18);
}
.search {
  margin: 10px 0 10px 15px;
  @include adaptiv-value("margin-top", 10, 5);
  @include adaptiv-value("margin-bottom", 10, 5);
  @include adaptiv-value("margin-right", 0, 0);
  @include adaptiv-value("margin-left", 15, 5);
  border: 0;
  @include adaptiv-value("padding-top", 10, 5);
  @include adaptiv-value("padding-bottom", 10, 5);
  @include adaptiv-value("padding-right", 15, 2);
  @include adaptiv-value("padding-left", 15, 0);
  flex: 1 1 auto;
}
.move {
  animation: 0.5s error infinite linear;
}
@keyframes error {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(10px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}
button:hover {
  @media (min-width: 768.01px) {
    padding: 20px 45px;
  }
}
</style>