<template>
  <div v-show="isSearching" class="cancel"><span @click="cancelSearch">Отменить поиск</span></div>
  <my-loading v-show="isFoodLoading"></my-loading>
  <div
      class="menu"
      :class="{
            'sale-menu': limit < 10
          }"
      v-if="searching.length">
      <food-item
          class="menu-item"
          v-for="food in searching.filter(s => s.id <= limit)"
          @addCount="addCountFood"
          @subCount="subCountFood"
          :food="food"
          :class="{
            'sale-item': limit < 10
          }"
          @open="openFood"
          :key="food.id">
      </food-item>
  </div>
  <my-dialog
      class="info"
      v-model:show="dialogVisible">
    <h3>Подробнее о блюде</h3>
    <table>
      <tr>
        <td>Название: </td>
        <td>{{ currentFood.title }}</td>
      </tr>
      <tr>
        <td>Описание: </td>
        <td>{{ currentFood.body }}</td>
      </tr>
      <tr>
        <td>Категория: </td>
        <td>{{ currentFood.categoryFood }}</td>
      </tr>
      <tr>
        <td>Продукт: </td>
        <td>{{ currentFood.category }}</td>
      </tr>
      <tr>
        <td>Калории: </td>
        <td>{{ currentFood.calories }} ккал / 100 г.</td>
      </tr>
      <tr>
        <td>Цена: </td>
        <td>{{ currentFood.price }} ₽</td>
      </tr>
      <tr>
        <td>Количество: </td>
        <td>
          <div class="table-buy">
            <div
                @click="subCountFood(currentFood.id)"
                class="table-buy__sub">-</div>
            <div class="table-buy__count">{{ currentFood.count }}</div>
            <div
                @click="addCountFood(currentFood.id)"
                class="table-buy__add">+</div>
          </div>
        </td>
      </tr>
    </table>
  </my-dialog>
  <transition name="fade">
    <div
        v-show="isMax"
        class="max">Максимум 10!
    </div>
  </transition>
  <h2 v-if="!searching.length && !isFoodLoading" style="text-align: center">Нет совпадений!</h2>
</template>

<script>
import FoodItem from "@/components/FoodItem";
import axios from "axios";
export default {
  components: {FoodItem},
  data() {
    return {
      foods: [],
      foodChoice: [],
      isFoodLoading: false,
      dialogVisible: false,
      currentFood: [],
      isMax: false,
      isSearching: false,
      searching: [],
      category: [
        { id: 1, title: 'Супы', url: 'https://via.placeholder.com/600/92c952' },
        { id: 2, title: 'Вторые блюда', url: 'https://via.placeholder.com/600/771796' },
        { id: 3, title: 'Салаты', url: 'https://via.placeholder.com/600/24f355' },
        { id: 4, title: 'Десерты', url: 'https://via.placeholder.com/600/d32776' }
      ]
    }
  },
  props: {
    search: {
      type: String
    },
    limit: {
      type: Number,
      required: true
    },
    searchCategory: {
      type: String,
    }
  },
  methods: {
    getRandomArbitrary(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    cancelSearch() {
      this.isSearching = false
      this.searching = this.foods
      this.$emit('cancel', false)
    },
    changePage(pageNumber) {
      this.page = pageNumber
      this.fetchPosts()
    },
    openFood(food) {
      this.dialogVisible = true
      this.currentFood = food
      document.body.style.overflow = 'hidden';
    },
    async fetchPosts() {
      try {
        this.isFoodLoading = true
          const response = await axios.get('https://jsonplaceholder.typicode.com/photos', {
            params: {
            _page: this.page,
              _limit: this.limit
        }
          })
        this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit)
        this.foods = response.data
        const responseBody = await axios.get('https://jsonplaceholder.typicode.com/comments')
        this.foodChoice = response.data.filter(t => t.id <= 30)
        this.foods.forEach(f => {
          f.price = this.getRandomArbitrary(250, 1500)
          f.calories = this.getRandomArbitrary(50, 300)
          const rand = Math.floor(Math.random()*this.foodChoice.length);
          f.category = this.foodChoice[rand].title;
          f.body = responseBody.data[rand].body.charAt(0).toUpperCase() + responseBody.data[rand].body.slice(1)
          f.title = f.title.charAt(0).toUpperCase() + f.title.slice(1)
          f.count = this.getRandomArbitrary(0, 2)
          const randCategory = Math.floor(Math.random()*this.category.length);
          f.categoryFood = this.category[randCategory].title;
        })
        this.foods.forEach(f => {
          f.category = f.category.match(/([a-zа-яё]+)/i)[0]
        })
        this.searching = this.foods
        if (this.search !== '' && this.search !== undefined) {
          this.reload()
        }
        if (this.searchCategory !== '' && this.searchCategory !== undefined) {
          let newFoods = []
          this.searching.forEach(f => {
            if (this.searchCategory.toLowerCase() === f.category.toLowerCase() || this.searchCategory.toLowerCase() === f.categoryFood.toLowerCase()) {
              newFoods.push(f)
            }
          })
          this.searching = newFoods
        }
        this.isFoodLoading = false
      } catch (e) {
        alert('Error')
        console.log(e)
      }
    },
    addCountFood(id) {
      const item = this.foods.find(t => t.id === id)
      if (item.count < 10) {
        item.count++
      } else {
        this.isMax = true
        setTimeout(() => {
          this.isMax = false
        }, 500)
      }
    },
    subCountFood(id) {
      const item = this.foods.find(t => t.id === id)
      if (item.count > 0) {
        item.count--
      }
    },
    reload(search) {
      this.searching = [...this.searching].filter(food => food.title.toLowerCase().includes(search.toLowerCase()))
      if (search !== undefined && search !== '') {
        this.isSearching = true
      }
    }
  },
  mounted() {
      this.fetchPosts()
    if (this.search === '' || this.search === undefined) {
      this.reload(this.search)
    }
  },
  watch: {
    search() {
      this.reload(this.search)
    },
    searchCategory() {
      sessionStorage.setItem('category', this.searchCategory)
      this.$router.push('/menu')
    }
  },
}
</script>

<style lang="scss" scoped>
@mixin adaptiv-value($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{1120 + px}) {
    #{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 390px) / #{1120 - 390}));
  }
}
.info {
  @include adaptiv-value("font-size", 20, 14);
}
.info h3 {
  padding: 10px;
  font-weight: 600;
  @include adaptiv-value("font-size", 22, 16);
}
table {
  max-width: 500px;
  width: 100%;
  border-spacing: 10px;
}
table tr td:first-child {
  font-weight: 500;
}
table tr {
  vertical-align: top
}
table td:last-child tr {
  vertical-align: center;
}
.table-buy__count {
  width: 20px;
  text-align: center;
}
.table-buy {
  display: flex;
  gap: 10px;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 123.3333333333%;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.table-buy__sub, .table-buy__add {
  width: 30px;
  text-align: center;
  border-radius: 25px;
  border: 1px solid black;
  cursor: pointer;
}
.max {
  position: fixed;
  z-index: 1000;
  @include adaptiv-value("right", 25, 10);
  @include adaptiv-value("bottom", 25, 10);
  background: #fff;
  border: 3px solid #ffe7a8;
  box-sizing: border-box;
  border-radius: 45px;
  @include adaptiv-value("padding", 15, 10);
  @include adaptiv-value("font-size", 20, 14);
  line-height: 123.3333333333%;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
.cancel {
  font-style: italic;
  float: right;
  margin: 0 0 10px 0;
  @include adaptiv-value("font-size", 20, 16);
}
.cancel span {
  cursor: pointer;
}
.menu-item:not(:last-child) {
  @include adaptiv-value("padding-bottom", 20, 10);
  @include adaptiv-value("margin-bottom", 20, 10);
  border-bottom: 1px solid #4f4f4f;
}
.menu-item:last-child {
  margin: 0 0 50px 0;
}
.sale-menu {
  display: flex;
  flex-wrap: wrap;
  @include adaptiv-value("gap", 30, 15);
}
.menu-item {
  @media (max-width: 600px) {
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
  }
}
.sale-item {
  background: #ffe7a8;
  box-shadow: 10px 8px 4px rgba(0, 0, 0, 0.25);
  border-radius: 45px;
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  gap: 15px;
  flex: 1 1 40%;
  align-items: center;
}
.sale-item:not(:last-child) {
  margin: 0;
  padding: 20px;
  border-bottom: 0;
}
.sale-item:last-child {
  margin: 0;
  padding: 20px;
}
</style>